body {
    background-color: rgb(211, 211, 211);
    overflow-y: scroll;
    position: absolute;
    margin: 0;
    overflow-x: hidden;
    font-family: Arial, Helvetica, sans-serif;
}

::-webkit-scrollbar {
    width: 8px;
}


::-webkit-scrollbar-thumb {
    background: #0008aa80;
    border-radius: 4px;
}
